@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;800;900&display=swap);

.menuItem {
    padding: 3rem 0;
    background: var(--blanco);
    outline: var(--outline);
    outline-offset: -1rem;
    text-align: center;
    box-shadow: var(--box-shadow);
    transition: .2s linear;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

img {
    margin: 1rem 0;
    width: 25rem;
    height: 18rem;
}

h3 {
    font-family: var(--font-title);
    font-size: 2rem;
}

.menuItem-container {
    margin: 2rem auto;
}

.menu {
    text-align: center;
}

.menu .btnplus {
    margin-top: 3rem;
    width: 15%;
}
.comments {
    padding: 4rem;
    width: 90%;
    max-width: 900px;
    margin: 2rem auto;
    background-color: var(--light-color);
    border-radius: 2rem;
}
.menu {
    margin: 0 auto 100px auto;
    text-align: center;
    width: 80%;
    position: relative;
}

.menuItem-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
    grid-gap: 1.5rem;
    gap: 1.5rem;
    margin-top: 30px;
}

.menuItem {
    padding: 3rem 2rem;
    background: var(--blanco);
    outline: var(--outline);
    outline-offset: -1rem;
    text-align: center;
    box-shadow: var(--box-shadow);
    transition: .2s linear;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.menuItem:hover {
    outline: var(--outline-hover);
    outline-offset: 0rem;
}

.menuItem img {
    margin: 1rem 0;
    width: 25rem;
    height: 18rem;
}

.menuItem-container h3 {
    font-family: var(--font-title);
    font-size: 2rem;
}

.menuItem-container p {
    font-family: var(--font-title);
    font-size: 2rem;
}

.loading {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.meal-details {
    background: var(--rojo);
    border-radius: 1rem;
    color: var(--blanco);
    margin: 0 auto;
    margin-top: 10rem;
    width: 35%;
    padding: 2rem 0;
    text-align: center;
    position: relative;
}

.meal-content {
    margin: 2rem;
}

.close {
    position: absolute;
    right: 2rem;
    top: 2rem;
    font-size: 2rem;
    background: var(--blanco);
    border: none;
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0.8;
}

.close:hover {
    opacity: 1;
}

.recipe-title {
    letter-spacing: 1px;
    padding-bottom: 1rem;
    font-size: 4rem;
    font-family: var(--font-title);
}

.recipe-ingredient {
    background: var(--blanco);
    font-weight: 600;
    color: var(--rojo);
    display: inline-block;
    padding: 0.5rem 0.8rem;
    border-radius: 0.3rem;
    font-size: 1.5rem;
    margin-bottom: 2rem;
}

.recipe-instructions {
    padding: 1rem 0;
}

.recipe-instructions p {
    font-size: 1.2rem;
    width: 80%;
    margin: 0 auto;
    font-family: var(--font-txt);
}

.recipe-img {
    margin: 0 auto;
    width: 80%;
}

.recipe-img img {
    border-radius: 1rem;
    width: 80%;
}

@media screen and (max-width: 1100px) {
    .meal-details {
        width: 90%;
    }
}
.cardsOne {
    box-shadow: var(--box-shadow);
